    display: inline-block;
    vertical-align: middle; }

.heading-section {
  margin-top: 80px; }
  .heading-section__inner {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center; }
  .heading-section .form-side {
    width: 430px;
    background: #303d5f;
    padding: 50px 60px; }
  .heading-section .form-heading {
    text-align: center;
    color: #fff;
    font-weight: 300;
    margin: 0 0 35px;
    font-size: 20px; }
  .heading-section input {
    width: 100%;
    display: block;
    height: 45px;
    margin: 0;
    padding: 0 0 0 20px;
    font-size: 14px;
    font-weight: 300;
