    border: none; }
    .heading-section input.err {
      border: 1px solid red; }
  .heading-section span {
    min-height: 15px;
    display: block;
    color: red; }
  .heading-section select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    cursor: pointer;
    padding-right: 25px;
    background: url(../img/arr-d.png) right 10px center no-repeat #fff;
    display: block;
    width: 100%;
    height: 45px;
    padding-left: 20px;
    outline: 0;
    border-radius: 6px; }
  .heading-section .terms-label {
    color: #fff;
    margin-bottom: 15px; }
    .heading-section .terms-label input {
      width: 20px;
      display: inline-block;
      vertical-align: middle;
      margin: 0 10px 0 0; }
    .heading-section .terms-label a {
      color: #fff; }
  .heading-section input[type=submit] {
    text-transform: uppercase;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    font-weight: 500;
    padding: 0;
    height: 60px;
    background-color: #fe2e68;
    background-image: linear-gradient(to top, rgba(222, 0, 62, 0.49) 0%, rgba(255, 67, 94, 0.49) 100%);
    border: 1px solid #a9002f;
    border-radius: 4px;
    box-shadow: inset 0 2px 0 rgba(255, 161, 199, 0.48);
    margin-bottom: 0; }
    .heading-section input[type=submit]:hover {
      background-image: linear-gradient(to bottom, rgba(222, 0, 62, 0.49) 0%, rgba(255, 67, 94, 0.49) 100%); }
  .heading-section .code {
    width: 55px;
    text-align: center;
    padding: 0 5px;
    float: left; }
  .heading-section .number {
    margin-left: 70px;
    width: calc(100% - 70px); }
  .heading-section .text-side {
    flex: 1;
    padding-right: 50px;
    color: #fff;
    margin-bottom: 150px; }
    .heading-section .text-side h1 {
      margin: 0 0 30px;
      font-weight: 100;
      text-transform: uppercase;
      font-size: 45px; }
    .heading-section .text-side p {
      font-size: 18px;
      line-height: 28px;
      font-weight: 300;
      margin: 0; }

.why-we {
  margin-top: 120px; }

.why-we__inner h2 {
  text-align: center;
  text-transform: uppercase;
  color: #1a243c;
  font-size: 40px;
  font-weight: 300;
  margin: 0 0 40px; }

.why-we__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between; }
  .why-we__list li {
    padding: 0 15px;
    width: 25%;
    text-align: center; }
    .why-we__list li img {
      display: block;
      max-width: 100%;
      height: auto;
      margin: auto; }
    .why-we__list li p {
      font-size: 19px;
      font-weight: 400; }

.awards {
  background: url(../img/awards-bg.jpg) top center no-repeat;
  background-size: cover;
  margin-top: 60px;
  padding: 50px 0; }

.our-awards {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
  color: #fff; }
  .our-awards li {
    display: inline-block;
    text-align: left;
    vertical-align: middle;
    max-width: 370px;
    margin: 0 20px; }
    .our-awards li .logo-side {
      float: left; }
      .our-awards li .logo-side img {
        max-width: 100%;
        height: auto;
        display: block; }
    .our-awards li .txt-side {
      margin-left: 185px;
      margin-top: 30px; }
      .our-awards li .txt-side .heading {
        margin: 0 0 5px;
        font-weight: 400; }
      .our-awards li .txt-side p {
        font-weight: 300;
        font-size: 14px;
        margin: 0; }
    .our-awards li:last-child .txt-side {
