.logos {
  margin: 50px 0;
  padding: 0;
  list-style-type: none;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center; }
  .logos li {
    margin: 0 5px 10px; }

.footer-top {
  padding: 15px 0;
  margin-bottom: 20px;
  border-bottom: 1px solid #9299ac; }
  .footer-top p {
    margin: 0; }
  .footer-top .copyright {
    float: left;
    font-weight: 300; }
  .footer-top .made-by {
    float: right;
    font-weight: 300; }
    .footer-top .made-by a {
      color: #fe2e68;
      text-decoration: none; }

.footer-bottom {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between; }
  .footer-bottom .bottom-txt {
    flex: 1;
    padding-right: 50px;
    margin: 0;
    color: #9299ac;
    font-size: 12px;
    font-weight: 300; }
  .footer-bottom .sert {
    font-weight: 300;
    font-size: 14px;
    padding-left: 44px;
    background: url(../img/gerb.png) top 5px left no-repeat;
    padding-top: 3px;
