    width: 310px; }

.errors {
  color: red;
  margin-top: 5px; }

@media screen and (max-width: 991px) {
  .all-wrapper {
    background-position: top left -350px; }
  .heading-section .form-side {
    padding: 25px 35px;
    width: 375px;
    margin-bottom: 100px; }
  .heading-section .form-heading {
    margin-bottom: 17px; }
  .heading-section .text-side h1 {
    font-size: 35px;
    margin-bottom: 20px; }
  .heading-section .text-side {
    margin-bottom: 0; }
  .our-awards li {
    display: block;
    margin: 0 auto 40px; }
    .our-awards li:last-child {
      margin-bottom: 0; } }

@media screen and (max-width: 768px) {
