    display: block; }
  .heading-section {
    margin-top: 20px; }
  .heading-section .form-side {
    background: rgba(48, 61, 95, 0.6);
    width: 100%;
    margin-top: 20px;
    margin-bottom: 0; }
  .heading-section .text-side {
    padding: 0;
    text-align: center; }
  .why-we {
    margin-top: 50px; }
  .why-we__inner h2 {
    font-size: 28px; }
  .language p {
    display: none; }
  .why-we__list li {
    width: 50%;
    margin-bottom: 15px; }
  .why-we__list {
    flex-flow: row wrap; }
  .footer-top .copyright, .footer-top .made-by {
    float: none;
    text-align: center; }
  .logos {
    margin: 30px 0; }
  .footer-bottom {
    display: block;
    text-align: center; }
  .footer-bottom .sert {
