    .our-awards li .logo-side img {
      margin: auto; }
  .our-awards li .txt-side {
    margin-left: 0; }
  .our-awards li:last-child .txt-side {
    margin-left: 0; }
  .footer-bottom .bottom-txt {
    padding: 0; }
  .footer-bottom .sert {
    width: 100%; } }
